const H2 = ({
  id,
  children,
  center,
  color = '#fff'
}: { id: string; children: React.ReactNode, center?: boolean, color?: string }) => ((
  <h2 id={id || ''}>
    { children }

    <style jsx>{`
      h2 {
        font-size: 24px;
        color: ${ color };
        ${ center && 'text-align: center;' }
      }
    `}</style>
  </h2>
));

export default H2;
